.App {
  text-align: center;
  padding: 0 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

div.form-selector, div.list-item {
  width: 100%;
  min-width: 300px;
  max-width: 500px;
  margin-bottom: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
}

div.form-selector > div {
  width: 100%;
  min-width: 300px;
  max-width: 500px;
}

div.form-selector > input {
  width: 100%;
  font-size: 16px;
  padding: 0 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.form-label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: start;
  padding: 5px 0 5px;
}

.list-item {
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #ccc;
  border-radius: 3px;
  height: 40px;
  margin-bottom: 10px;
}

.list-item:hover {
  background-color: #d3dcf5;
}

.list-item {
  cursor: pointer;
}

.list-item > a > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.list-item > a {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  text-decoration: none;
  color: #222;
}

.no-syllabus div {
  color: #bbb;
}

.list-item > a > div:nth-child(1) {
  width: 27%;
}
.list-item > a > div:nth-child(2) {
  width: 40%;
}
.list-item > a > div:nth-child(3) {
  width: 8%;
}
.list-item > a > div:nth-child(4) {
  width: 8%;
}
.list-item > a > div:nth-child(5) {
  width: 17%;
}

@media (max-width: 400px) {
  .list-item > a > div:nth-child(1) {
    display: none;
  }
  .list-item > a > div:nth-child(2) {
    width: 50%;
  }
  .list-item > a > div:nth-child(3) {
    width: 15%;
  }
  .list-item > a > div:nth-child(4) {
    width: 15%;
  }
  .list-item > a > div:nth-child(5) {
    width: 20%;
  }
}

@media (max-width: 800px) {
  .github-ribbon {
    visibility: hidden;
  }
}



